			        <span class="icon iconfont icon-xiugai19" :style='{"margin":"0 2px","fontSize":"14px","color":"#0E9BD3","height":"40px"}'></span>
			        编辑
			      </el-button>
				  <el-button class="list1-btn8" v-if="isAuth('jiaofeitongzhi','缴费')" @click="jiaofeixinxiCrossAddOrUpdateHandler(item,'cross','','jiaofeizhuangtai','已缴费','已缴费,未缴费'.split(',')[0])">
				    <span class="icon iconfont icon-qita6" :style='{"margin":"0 2px","fontSize":"14px","color":"#fff","height":"40px"}'></span>
				    缴费
				  </el-button>
			      <el-button class="list1-del" v-if="isAuth('jiaofeitongzhi','删除') " @click="deleteHandler(item.id)">
			        <span class="icon iconfont icon-shanchu15" :style='{"margin":"0 2px","fontSize":"14px","color":"#0E9BD3","height":"40px"}'></span>
