	  	  width: 60px;
	  	  font-size: 32px;
	  	  line-height: 60px;
	  	  text-align: center;
	  	  height: 60px;
	  	}
	
	.add-update-preview /deep/ .el-upload-list .el-upload-list__item {
	  	  border: 2px dashed #C8C8C8;
	  	  cursor: pointer;
	  	  border-radius: 6px;
	  	  color: #C8C8C8;
	  	  width: 60px;
	  	  font-size: 32px;
	  	  line-height: 60px;
